  /** 获取用户权限ID */
    getPermiessionId(roleId){
      let _jsondata_ =   {
        role_id:roleId
      } ;
      var that = this;
      //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.platformRole_queryRoleByIds,// 方法路径
        this.customGlobal.serverUrl.orgBaseURI,//服务器地址
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
      );
      //post提交后返回值操作
      p1.then((res) => {
        //  console.log("异步调用成功返回:"+JSON.stringify(res));
        if(res.data.status=='success') {
          let jsonObj=res.data.rows;
          let roleIds= {};
          //将查询出来的权限ID分别赋值给tableRowIdMap以及一个用于回显的自定义数组
          if(jsonObj.length>0){
            for(let item in jsonObj) {
              this.tableRowIdMap.set(jsonObj[item].rp_p_id, true);
              roleIds[jsonObj[item].rp_p_id]=true;
            }
            this.menuNum=jsonObj.length;
          }
          //先清空再回显
          this.noCheckTreeTable(that.tableTreeData)
          this.initTableDataPermissionStatus(roleIds,that.tableTreeData);

        }else{
        //  console.info("查询失败");
          // loading.close();
          that.tableTreeData = [];
          this.$alert(res.data.msg, '查询失败', {
            confirmButtonText: '确定',
            callback: action => {
            //  console.info('data:'+JSON.stringify(res.data));
              if(res.data.msg==='请登录认证' && res.data.status==='failed'){
                this.$router.replace({path:'/login'});
              }
            }
          });
        }
      },error=>{
      //  console.log("异步调用出错:"+JSON.stringify(error));
        // loading.close();
        that.tableTreeData = [];
        this.$alert(JSON.stringify(error.message), '查询出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
      //  console.log("异步调用失败返回:"+JSON.stringify(res));
        that.tableTreeData = [];
        this.$alert(JSON.stringify(res.message), '查询出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },

