  /** 表单提交 */
    submitForm(formName) {
      let url_method='';
      let msg_show='';
      let flag=this.tableOperFlag;
      if(this.queryOrgType>this.checkOrgTreeType){
        this.$alert('您还未获取此机构的操作权限，请联系系统管理员！', '新增出错!', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
        return;
      }
      switch (flag) {
        case 1:
          url_method=this.customGlobal.serverUrl.thermalAccount_add;
          msg_show="添加";
          break;
        case 2:
          url_method=this.customGlobal.serverUrl.thermalAccount_update;
          msg_show="编辑";
          break;
      }
      //将存储角色ID的map转换为数组
      let objPermissionKey={};
      this.tableRowIdMap.forEach(function(value,key){
        objPermissionKey[key]=true;
      });
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let _jsondata_ =   {
            cui_uuid:this.thermalUserForm.cui_uuid,
            cui_login_id:this.thermalUserForm.cui_login_id,
            cui_password:this.customGlobal.md5(this.thermalUserForm.cui_password),
            cui_realname:this.thermalUserForm.cui_realname,
            cui_wx_nick_name:this.thermalUserForm.cui_wx_nick_name,
            cui_concat_phone:this.thermalUserForm.cui_concat_phone,
            cui_wx_open_id:this.thermalUserForm.cui_wx_open_id,
            cui_wx_union_id:this.thermalUserForm.cui_wx_union_id,
            cui_status:this.thermalUserForm.cui_status==true?'1':'2',
            check_org_id:this.checkOrgTreeId,
            check_org_type:this.checkOrgTreeType,
            cui_company_id:this.thermalUserForm.cui_company_id,
            cui_child_company_id:this.thermalUserForm.cui_child_company_id,
            cui_institute_id:this.thermalUserForm.cui_institute_id,
            cui_station_id:this.thermalUserForm.cui_station_id,
            cui_admin_type:this.thermalUserForm.cui_admin_type==true?'0':'1',
            permission_id:objPermissionKey,
            org_id:this.userOrgIds, //后台创建redis信息使用
            update_org_type:this.thermalUserForm.update_org_type,//更新时查询是否存在管理员使用

          } ;
          const loading = this.$loading({
            lock: true,
            text: '提交中...',
            target: '.main',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, .7)'
          });
          //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
          var p1 = this.customGlobal.postHttpClient(
            url_method,// 方法路径
            this.customGlobal.serverUrl.orgBaseURI,//服务器地址
            {"Content-Type": "application/x-www-form-urlencoded"},
            this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
          );
          //post提交后返回值操作
          p1.then((res) => {
            //  console.log("异步调用成功返回:"+JSON.stringify(res));
            if(res.data.status=='success'){
              //  console.info("===机构ID：============"+JSON.stringify(this.userOrgIds));
              this.thermalUserFormVisible=false;

              //提交之后 清空表单及系统公用变量值，防止数据出错
              this.tableRowIdMap=new Map();
              this.noCheckTreeTable(this.tableTreeData);

              this.queryValveCurrentData();
              loading.close();
              this.$message({
                type: 'success',
                message: msg_show+'成功!'
              });
            }else{
            //  console.info("编辑失败");
              loading.close();
              this.$alert(res.data.msg, msg_show+'失败', {
                confirmButtonText: '确定',
                callback: action => {
                //  console.info('data:'+JSON.stringify(res.data));
                  if(res.data.msg==='请登录认证' && res.data.status==='failed'){
                    this.$router.replace({path:'/login'});
                  }
                }
              });
            }
          },error=>{
            loading.close();
          //  console.log("异步调用出错:"+JSON.stringify(error));
          }).catch((res) => {
            loading.close();
          //  console.log("异步调用失败返回:"+JSON.stringify(res));
          });

        } else {
        //  console.log('error submit!!');
          return false;
        }
      });
