      // this.treeCheckedKeys = node.checkedKeys;
      this.checkAllOrgIds=this.checkAllOrgIds.reverse();

     //  console.log("====机构树checkBox选中事件====选中的ID============="+node.checkedKeys)
    },

  /** 查询选中的机构ID的所有父节点 */
    recursion(arr,isChild) {
      let aCheck = [];
      for (let i = 0; i < arr.length; i++) {
        let obj = arr[i];
        aCheck[i] = false;
        if (obj.detail) {
          aCheck[i] = this.recursion(obj.detail, true) ? true : aCheck[i];
          if (aCheck[i]) {
            this.checkAllOrgIds.push(obj['id']);
          }
        }
        for (let j = 0; j < this.checkOrgId.length; j++) {
          if (obj['id'] == this.checkOrgId[j]) {
            aCheck[i] = true;
            if (this.checkAllOrgIds.indexOf(obj['id']) == -1) {
              this.checkAllOrgIds.push(obj['id']);
            }
            break;
          }
        }
      }
      if (isChild) {
        return aCheck.indexOf(true) != -1;
      }
    },

  /** 清空tree的复选框 */
    noCheckOrgTree(rows){
      this.$refs.thermalOrgTree.setCheckedKeys(rows)
    },

  /** 删除机构树节点 */
    removeTreeNode (node, data) {
      const parent = node.parent
      const children = parent.data.detail || parent.data
      const index = children.findIndex(d => d.id === data.id)
      children.splice(index, 1)
    },

  /** 判断那些节点需要显示 */
    initTreeDataStatus(h, { node, data, store }) {
      if (JSON.stringify(this.treeDataStatus) === "{}") {
        return (
          <span style="font-size:14px;">
               <span>{data.caption}</span>
            </span>
        )
      } else {
        let find = false;
        for (let k of Object.keys(this.treeDataStatus)) {
          if (data.id === k) {
            return (
              <span style="font-size:14px;">
                   <span>{data.caption}</span>
                </span>
            )
            find = true;
            break;
          }
        }
        if (find === false) {
          this.removeTreeNode(node, data);
        }
      }

    },

  /** 将用户的机构权限分类放到数组中1  */
    initAccOrgTreeData(data,data2){
      // 判断用户类型
      if(this.customGlobal.userInfoExt.cui_type==='0'&&this.customGlobal.userInfoExt.cui_org_type==='0'){
        this.selectAdminOrgIds(data);
        return;
      }
      for (let k of Object.keys(data2)) {
          this.selectOrgIds(data,k);
      }
    },

  /** 将用户的机构权限分类放到数组中2  */
    selectOrgIds(row,id){
      for (let i = 0; i < row.length; i++) {
        if(row[i].id===id){
          if(row[i].type>=this.customGlobal.userInfoExt.cui_type) {
            switch (row[i].type) {
                case 1:
                  this.companyIds.push(row[i].id);
                  break
                case 2:
                  this.childCompanyIds.push(row[i].id);
                break
                case 3:
                  this.instituteIds.push(row[i].id);
                break
                case 4:
                  this.stationIds.push(row[i].id);
                break
            }
          }
        }
        if(row[i].detail){
          this.selectOrgIds(row[i].detail,id);
        }
      }

    },

  /** 将用户的机构权限分类放到数组中 超级管理员使用，拥有全部机构ID  */
    selectAdminOrgIds(row){
      for (let i = 0; i < row.length; i++) {
        switch (row[i].type) {
          case 1:
            this.companyIds.push(row[i].id);
            break
          case 2:
            this.childCompanyIds.push(row[i].id);
            break
          case 3:
            this.instituteIds.push(row[i].id);
            break
          case 4:
            this.stationIds.push(row[i].id);
            break
        }
       // this.userOrgAngType.push(id);
        if(row[i].detail){
          this.selectAdminOrgIds(row[i].detail);
        }
      }

    },

  /** 查询出所有机构树信息 */
    initOrgTemplateInfo(){
      const loading = this.$loading({
        lock: true,
        text: '查询中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, .7)'
      });
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postQueryOrganTemplate,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata:JSON.stringify({config:'all',username:this.customGlobal.userInfoExt.cui_login_id})})
      );

      p1.then((res) => {
      //  console.log("异步调用成功返回:"+JSON.stringify(res));
        if(res.data.status=='success'){
          this.treeData =  JSON.parse(res.data.organ).detail;
          this.treeDataStatus = JSON.parse(res.data.orgkey);
          loading.close();

        }else{
       //   console.info("查询失败");
          this.treeData = [ JSON.parse(res.data.organ) ];
          this.treeDataStatus = JSON.parse(res.data.orgkey);
          loading.close();


        }

      },error=>{
      //  console.log("异步调用出错:"+JSON.stringify(error));
        loading.close();
        this.$alert(JSON.stringify(error.message), '保存出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
      //  console.log("异步调用失败返回:"+JSON.stringify(res));
        loading.close();

        this.$alert(JSON.stringify(res.message), '保存出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });

    },

  /** 提交热力用户权限标识 */
    submitUserOrg(){
      if(this.checkAllOrgIds.length==0){
        this.$alert("机构信息不能为空或您未做变更，无法提交！", '保存热力用户机构信息', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
        return;
      }

      if(this.selType<this.customGlobal.userInfoExt.cui_org_type){
        this.$alert("您还未取得此机构的权限！", '保存热力用户机构信息', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
        return;
      }
      let objOrgId={};
      for (let i = 0; i < this.checkAllOrgIds.length; i++) {
        objOrgId[this.checkAllOrgIds[i]]=true
      }
      //保存用户机构权限
      this.saveUserOrgKeys(this.loginIdByOrg,objOrgId);
      //清空用户的选择
      this.$nextTick(() => {
        this.$refs.thermalOrgTree.setCheckedKeys(this.checkAllOrgIds);
      });
      this.userOrgId=[];
      this.platformUserOrgVisible=false;
      //  console.log("==========用户权限选择============"+this.userOrgId);
    },

  /** 保存用户机构权限 */
    saveUserOrgKeys(loginId,objOrgId){
      let _jsondata_ =   {
        username:loginId,
        orgkey:objOrgId
      } ;
      const loading = this.$loading({
        lock: true,
        text: '操作中...',
        target: '.main',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, .7)'
      });
      //  console.info("===保存用户机构权限=====提交参数==="+JSON.stringify(_jsondata_));
      //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postCreateUserAndOrgKey,// 方法路径
        this.customGlobal.serverUrl.orgBaseURI,//服务器地址
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
      );
      //post提交后返回值操作
      p1.then((res) => {
        // console.log("异步调用成功返回:"+JSON.stringify(res));
        this.thermalUserOrgVisible=false;
        loading.close();
        if(res.data.status=='success'){
          this.$message({
            type: 'success',
            message: '热力用户机构信息保存成功!'
          });
        }else{
          this.$alert("请联系系统管理员，并提交错误报告："+JSON.stringify(error.message), '保存用户机构权限出错!', {
            confirmButtonText: '确定',
            callback: action => {
            }
          });
        }
      },error=>{
        loading.close();
      //  console.log("异步调用出错:"+JSON.stringify(error));
        this.$alert(JSON.stringify(error.message), '保存用户机构权限出错!', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
      }).catch((res) => {
        loading.close();
      //  console.log("异步调用失败返回:"+JSON.stringify(res));
        this.$alert(JSON.stringify(res.message), '保存用户机构权限出错', {
          confirmButtonText: '确定',
          callback: action => {
          }
        });
      });
    },

  },


}
</script>

<style scoped>
